import React from 'react';


class Header extends React.Component {
  
  constructor(props) {
    super(props)   
    this.state = {activeLink: this.props.activeLink};    
  }

  menuSelect(id) {    
    this.setState({
      activeLink: id
    });
  }

  
  
  render() {    
    const lang = this.props.lang;
    const links = [
      {
        id: "#wrapper",
        text: lang.ev        
      },
      {
        id: "#about",
        text: lang.hakkimda
      },
      {
        id: "#skills",
        text: lang.yetenekler
      },
      {
        id: "#experience",
        text: lang.deneyimler
      },
      {
        id: "#certificates",
        text: lang.header_sertifikalar
      },
      {
        id: "#contact",
        text: lang.iletisim
      }
    ];      
   
      return (      
        <header className="sticky-header menu-layout1">
          <div className="container">
            <div className="row d-flex align-items-center">
              <div className="col-lg-2">
                  <div className="logo-area">
                      <a href="index.html" className="default-logo">
                          <img src="https://affixtheme.com/html/ree/demo/light/media/logo-default.png" alt="logo" className="img-fluid" />
                      </a>
                      <a href="index.html" className="sticky-logo">
                          <img src="https://affixtheme.com/html/ree/demo/light/media/logo-sticky.png" alt="logo" className="img-fluid" />
                      </a>
                  </div>
              </div>
              <div className="col-lg-10 d-flex justify-content-end">
                  <nav id="dropdown" className="template-main-menu" style={{display: "block"}}>
                      <ul id="onepage_nav">
                      {
                        links.map(link => {

                        return ( 
                          <li className={this.state.activeLink === link.id ? "current" : ""} 
                              key={link.id} onClick={() => this.menuSelect(link.id)}>
                            <a href={link.id}>{link.text}</a>
                          </li>
                        );
                      })}                      
                      </ul>
                  </nav>
              </div>
          </div>
          </div>
        </header>
      );
    }
  }

export default Header;

