import Banner from './Banner';
import Header from './Header';
import About from './About';
import Skills from './Skills';
import Certificate from './Certificate';
import Experience from './Experience';
import React, { Component } from "react";
//import { instanceOf } from "prop-types";
import { withCookies, Cookies } from "react-cookie";
import {API_URL} from '../AppConfig';
import Contact from './Contact';

class App extends Component {

  constructor(props) {
    super(props);
    this.state = { 
      language: null,
      bannerInfo : {},
      aboutInfo: {},
      skills: {},
      experienceList :{},
      certificateList : {},
      userId : 1,
      loading: true,      
      activeLink : "#wrapper",
      footerInfo: {}
    }           
  }  

  pageLoad = ()  => { 
    
    const userId = this.state.userId;    
    fetch(API_URL + "/users?userId=" + userId, {
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      }
    })    
    .then(async response => {      
        let data = await response.json();                
        const language = this.props.cookies.get("language") || data.defaultLanguage || "tr";

        if (!response.ok) {            
            const error = (data && data.message) || response.statusText;
            return Promise.reject(error);
        }
        
        fetch(API_URL + "/details_"+ language+"?userId=" + userId)
        .then(responseDetails => responseDetails.json())
        .then(responseDetails =>{

          this.setState(
          {                        
            aboutInfo : responseDetails.aboutInfo,
            experienceList : responseDetails.experienceList,
            footerInfo: {
              mail: responseDetails.aboutInfo.mail,
              country: responseDetails.aboutInfo.country,
            }       
          });

        })
        .then(() => {      
          data = data[0];
          const bodyElement = document.body.classList;
         
          bodyElement.remove('loaded');

          this.setState(
          { 
            userLanguage: data.defaultLanguage,         
            bannerInfo : data.bannerInfo,
            skills : data.skills,
            socailList : data.socialAdreess,              
            certificateList : data.certificateList,
            language: language
            //loading: false           
          });  
         
          setTimeout(function(){            
              
              const locHash = window.location.hash;             
                             
              this.setState( { activeLink: locHash , loading: false });  
              if(locHash) {                
                document.querySelectorAll("a[href='"+locHash+"']")[0].click();                   
              }  

              setTimeout(function(){   
                      
                bodyElement.add('loaded'); 

                if (!!window.IntersectionObserver) {
                 
                  let observer = new IntersectionObserver((entries, observer) => {                    
                      entries.forEach(entry => {                        
                          if (entry.isIntersecting) {
                              entry.target.classList.add("active-animation");                              
                              observer.unobserve(entry.target);
                          }
                      });
                  }, {
                      rootMargin: "0px 0px -150px 0px"
                  });
                  document.querySelectorAll('.has-animation').forEach(block => {
                      observer.observe(block)
                  });
                }
                else {
                    document.querySelectorAll('.has-animation').forEach(block => {
                        block.classList.remove('has-animation')
                    });
                }                                                                                 

              }, 750);                 
            }.bind(this), 1500);    
        });

        return;                               
    })
    .catch(error => {
        this.setState({ errorMessage: error.toString() });
        console.error('There was an error!', error);
    });
  };

 
  componentDidMount(){    
    this.pageLoad ();
 } 

  //static propTypes = { cookies: instanceOf(Cookies).isRequired };  

  handleSetCookie = lang => {      
    if(this.state.language === lang) return false;    
    const { cookies } = this.props;  
    cookies.remove("language"); // remove the cookie
    cookies.set("language", lang, { path: "/" }); // set the cookie
    this.setState({ loading: true, language: cookies.get("language") });
    this.pageLoad();
  };

  render() {      
    const language = this.state.language;

    if(!language || this.state.loading) 
      return (
        <div className="preloader">
            <div className="preloader-gif">
              <img src= {window.location.origin + "/preloader.gif"} alt="Preloader" />
            </div>
        </div>);    
 
    const lanFolder = require(`../locales/${language}/translation.json`); 
    
    return (
          <React.Fragment>
            <Header lang={lanFolder} activeLink={this.state.activeLink}/>
            <Banner lang={lanFolder} bannerInfo={this.state.bannerInfo} socailList = {this.state.socailList} handleSetCookie={this.handleSetCookie}/>                        
            <About lang={lanFolder} aboutInfo={this.state.aboutInfo} bannerInfo={this.state.bannerInfo}/>
            <Skills lang={lanFolder} skills={this.state.skills} />
            <Experience lang={lanFolder} experienceList={this.state.experienceList} />  
            <Certificate lang={lanFolder} certificateList={this.state.certificateList}/>
            <Contact lang={lanFolder} footerInfo={this.state.footerInfo}/>                                                
          </React.Fragment>
    );
  }
}

export default withCookies(App);